export const ApproveProposalPage: React.FC<IApproveProposal.IProps> = (
		props: IApproveProposal.IProps
	): JSX.Element => {
	const { contractNode, clientData, SetBenefitNodes, SetClientData, exclusionList, SetExclusionList } = props;

	const classes = useStyles();
	const btnclasses = buttonStyles();
	const [currentStep, setCurrentStep] = useState(0);
	const [loading, setLoading] = useState(false);
	const [isApprovalModalOpen, setIsApprovalModalOpen] = useState(false);
	const [approvalModalTitle, setApprovalModalTitle] = useState("");
	const [approvalModalMessage, setApprovalModalMessage] = useState("");
	const [approvalModalButtons, setApprovalModalButtons] = useState<any>(null);
	const [allowRevision, setallowRevision] = useState(false);
	const [allowedRevise, setAllowedRevise] = useState(false);
	const [redirectToDashboard, setRedirectToDashboard] = useState(false);
	const [clientLoaded, setClientLoaded] = useState(false);
	const [nodesLoaded, setNodesLoaded] = useState(false);
	const [contractLoaded, setContractLoaded] = useState(false);
	const [invalidFields] = useState<string[]>([]);
	const [user_role] = useState("Underwriting Manager");
	const [accessToken] = useState('');
	// const [client_id] = useState("5db7fc70a251bafd2ee7c96d");

	// const renderTitle = (step) => {
	// 	switch(step) {
	// 	    case 0:
	// 	    	return "Details";
	// 	    case 1:
	// 	    	return "Modify Benefits";
	// 	    case 2: 
	// 	    	return "Review";
	// 	   	default:
	// 	      return '';
	// 	}
	// }


	const renderStep = (step) => {
		switch(step) {
		    case 0:
		    	return <Details submitMode invalidFields={invalidFields} classes={classes} {...props} />;
		    case 1:
		    	return <StandardBenefitPlan 
		    				submitMode 
		    				classes={classes} 
		    				{...props} 
		    			/>;
		    case 2: 
		    	return <Summary classes={classes} {...props} />;
		   	default:
		      return null;
		}
	}


	const setStepBack = () => {
		if (currentStep > 0) {
			setCurrentStep(currentStep - 1);
		}
	}

	const setStepForward = () => {
		if (currentStep < 2) {
			setCurrentStep(currentStep + 1);
		}
	}

	const successRedirectToDashboard = () => {
		setRedirectToDashboard(true);
	}

	const showHideApprovalModal = (open: boolean) => {
		setIsApprovalModalOpen(open);
	}

	const updateAllowedRevise = (flag: boolean) => {
		setAllowedRevise(flag)
	}

	const handleLoadClientFromAPI = () => {
		const GET_URL = API_URL + "clients/" + props.match.params.client_id;

		return fetch(GET_URL, {
		   	method: 'get',
		   	headers: {'Content-Type':'application/json'}
		}).then(response => response.json()
		).then((response: any) => {
			const data: any = Object.assign({}, response);

			const client = convertClientData(data);

			SetClientData(Object.assign({}, clientData, client));
			setClientLoaded(true);
		}).catch(error => {
			setClientLoaded(true);
			console.log('getbenefitlist api error: ', error);
		});
	}

	const handleLoadBaseNodesFromAPI = () => {
		setLoading(true);

		const GET_URL = API_URL + "base-benefit-plans/";
		// const GET_URL = 'http://18.138.94.224:4101/api/' + "base-benefit-plans/";

		return fetch(GET_URL, {
		   	method: 'get',
		   	headers: {'Content-Type':'application/json'}
		}).then(response => response.json()
		).then((response: any) => {
			const data: any[] = [...response];
			let nodes : any[] = [];

			nodes = convertNodeData(data);
				
			handleLoadBenefitNodesFromAPI(nodes);
		}).catch(error => {
			setLoading(false);
			console.log('getbenefitlist api error: ', error);
		});
	}

	const handleLoadBenefitNodesFromAPI = (baseNodesParam: any[]) => {
		const GET_URL = API_URL + "benefit-plans/tree/" + props.match.params.proposal_id;

		return fetch(GET_URL, {
		   	method: 'get',
		   	headers: {'Content-Type':'application/json'}
		}).then(response => response.json()
		).then((response: any) => {
			const data: any[] = [...response];
			let baseNodes: any [] = [...baseNodesParam];
			let benefitNodes : any[] = [];
			let eList : any[] = [exclusionList];

			benefitNodes = convertNodeData(data, true);
				
			let nodes: any[] = combineBaseBenefitNodes(baseNodes, benefitNodes);

			eList = createExclusionList(nodes);

			setNodesLoaded(true);
			SetExclusionList(eList)
			SetBenefitNodes (nodes);
		}).catch(error => {
			setNodesLoaded(true);
			console.log('getbenefitlist api error: ', error);
		});
	}

	const showSuccessModal = (message: string, title: string) => {
		setLoading(false);
		setApprovalModalTitle(title);
		setallowRevision(false);
		setAllowedRevise(false);
		setApprovalModalMessage(message);
		showHideApprovalModal(true);
		setApprovalModalButtons(
			<div>
				<Button
					className={btnclasses.okayBtn}
					variant="contained"
					color="secondary"
					onClick={successRedirectToDashboard}>
					Okay
				</Button>
			</div>
		)
	}

	const showFailedModal = (message: string, title: string) => {
		setLoading(false);
		setApprovalModalTitle(title);
		setallowRevision(false);
		setAllowedRevise(false);
		setApprovalModalMessage(message);
		showHideApprovalModal(true);
		setApprovalModalButtons(
			<div>
				<Button
					className={btnclasses.okayBtn}
					variant="contained"
					color="secondary"
					onClick={()=>showHideApprovalModal(false)}
				>
					Okay
				</Button>
			</div>
		)
	}

	const handleDisapproveProposal = () => {
		setLoading(true);
		showHideApprovalModal(false);
		const body = {
			"proposal_approved": false
		}
		Processmaker.put('cases/'+props.match.params.ticket_id+'/variable', accessToken, body)
		.then(response => {
			if(response && response.error !== undefined){
				const message: string = "Failed to disapprove proposal.";
				showFailedModal(message, "Disapprove Proposal")
			}else{
				Processmaker.put('cases/'+props.match.params.ticket_id+'/route-case',accessToken,{}).then(response2 => {
					console.log(response2);
					Processmaker.put('cases/'+props.match.params.ticket_id+'/route-case',accessToken,{}).then(response3 => {
						if(response3 && response3.error !== undefined){
							const message: string = "Failed to disapprove proposal.";
							showFailedModal(message, "Disapprove Proposal")
						}else{
							let message: string;
								message = "BDO was notified that the proposal "
									+ "is disapproved and can no longer be revised"
									+ " for further use.";
									showSuccessModal(message, "Proposal Disapproved")
						}
					});
				});
			}  
        });
	}

	const disapproveProposal = () => {
		const message = "Your comments will be sent back to the BDO agent."
			+ " You can choose to have the BDO revise this or reject"
			+ " the proposal altogether.";
		setApprovalModalTitle("Disapprove")
		setApprovalModalMessage(message)
		showHideApprovalModal(true);
		setallowRevision(true);
		setApprovalModalButtons(
			<div>
				 <Button
				 	className={btnclasses.cancelBtn}
					 variant="contained"
					 color="secondary"
					 onClick={()=>showHideApprovalModal(false)}>
                        Cancel
                </Button>
                <Button
					className={btnclasses.disapproveBtn}
					variant="contained"
					color="primary" onClick={handleDisapproveProposal}>
                    Disapprove
                </Button>
			</div>
		)
	}

	const handleApproveProposal = (withconditions: boolean) => {
		setLoading(true);
		showHideApprovalModal(false);
		const body = {
			"proposal_approved": true,
			"revisions": withconditions
		}
		Processmaker.put('cases/'+props.match.params.ticket_id+'/variable',accessToken, body)
		.then(response => {
			console.log("variable >>>>", response);
			if(response && response.error !== undefined){
				const message: string = "Failed to approve proposal.";
				if(withconditions === true){
					showFailedModal(message, "Approve Proposal with conditions");
				}else{
					showFailedModal(message, "Approve Proposal");
				}
			}else{
				Processmaker.put('cases/'+props.match.params.ticket_id+'/route-case',accessToken,{}).then(response2 => {
					console.log(response2);
					Processmaker.put('cases/'+props.match.params.ticket_id+'/route-case',accessToken,{}).then(response3 => {
						if(withconditions === true){
							Processmaker.put('cases/'+props.match.params.ticket_id+'/route-case',accessToken,{}).then(response4 => {
								if(response4 && response4.error !== undefined){
									const message: string = "Failed to approve proposal.";
									if(withconditions === true){
										showFailedModal(message, "Approve Proposal with conditions");
									}else{
										showFailedModal(message, "Approve Proposal");
									}
								}else{
									let message: string;
									message = "Proposal was successfully approved.";
									setApprovalModalTitle("Approve Proposal with conditions");
									setApprovalModalMessage(message)
									showHideApprovalModal(true);
									setApprovalModalButtons(
										<div>
											<Button
												className={btnclasses.okayBtn}
												variant="contained"
												color="secondary"
												onClick={successRedirectToDashboard}>
													Okay
											</Button>
										</div>
									)
								}
							});
						}else{
							if(response3 && response3.error !== undefined){
								const message: string = "Failed to approve proposal.";
								showFailedModal(message, "Approve Proposal");
							}else{
								let message: string;
									message = "Proposal was successfully approved.";
								setApprovalModalTitle("Approve Proposal");
								setApprovalModalMessage(message)
								showHideApprovalModal(true);
								setApprovalModalButtons(
									<div>
										<Button
											className={btnclasses.okayBtn}
											variant="contained"
											color="secondary"
											onClick={successRedirectToDashboard}>
												Okay
										</Button>
									</div>
								)
							}
						}
					});
				});
			}    
        });
	}

	const approveProposalWithConditions = () => {
		const message = "Your comments will be sent back to the BDO"
			+ " agent for revisions. Are you sure you want to "
			+ "approve this with conditions?";
		setApprovalModalTitle("Approve Proposal with conditions")
		setApprovalModalMessage(message)
		showHideApprovalModal(true);
		setApprovalModalButtons(
			<div>
				 <Button
				 	className={btnclasses.cancelBtn}
					 variant="contained"
					 color="secondary"
					 onClick={()=>showHideApprovalModal(false)}>
                        Cancel
                </Button>
                <Button
					className={btnclasses.approveWithConditionsBtn}
					variant="contained"
					color="primary" onClick={()=>handleApproveProposal(true)}>
                    Approve with conditions
                </Button>
			</div>
		)
	}

	const approveProposal = () => {
		let message:string = "";
		if(user_role === "Underwriting Manager"){
			message = "The BDO will be notified of your approval and"
				+ " the changes you added in this proposal. Are you"
				+ " sure you want to approve this?";
		}else{
			message = "You included several comments for this proposal. "
				+ "Are you sure you want to approve this and send it to"
				+ " the Underwriting Department?";
		}
		setApprovalModalTitle("Approve Proposal")
		setApprovalModalMessage(message)
		showHideApprovalModal(true);
		setApprovalModalButtons(
			<div>
				 <Button
				 	className={btnclasses.cancelBtn}
					 variant="contained"
					 color="secondary"
					 onClick={()=>showHideApprovalModal(false)}>
                        Cancel
                </Button>
                <Button
					className={btnclasses.approveBtn}
					variant="contained"
					color="primary" onClick={()=>handleApproveProposal(false)}>
                    Approve
                </Button>
			</div>
		)
	}

	useEffect(() => {
		setLoading(true);
		handleLoadClientFromAPI();
		handleLoadBaseNodesFromAPI();
	}, []);

	useEffect(() => {
		if (clientLoaded && nodesLoaded) {
			setLoading(false);
		}
	}, [clientLoaded, nodesLoaded]);

	useEffect(() => {
		if (!contractLoaded && contractNode) {
			const client = {
				principalData: {
					numOfPrincipals: contractNode.principals_count ? contractNode.principals_count : 0,
					malePrincipals: contractNode.male_principals_count ? contractNode.male_principals_count : 0,
					femalePrincipals: contractNode.female_principals_count ? contractNode.female_principals_count : 0,
					numOfDependents: contractNode.dependents_count ? contractNode.dependents_count : 0,
					maleDependents: contractNode.male_dependents_count ? contractNode.male_dependents_count : 0,
					femaleDependents: contractNode.female_dependents_count ? contractNode.female_dependents_count : 0,
				}
			};
			SetClientData(Object.assign({}, clientData, client));
			setContractLoaded(true);
		}
	}, [contractNode]);

	if(redirectToDashboard === true){
		return <Redirect to={{pathname: '/underwriting'}}/>
	}else{
		return (
			<div className={classes.main}>
				{ loading ?
					(<LoadingIndicator/>) : null

				}
				{renderStep(currentStep)}
				<FloatingFooter>

					{currentStep !== 0 ?
						<Button 
							variant="contained"
							onClick={setStepBack}
							id="proposal_previous"
							color="primary"
							className='button back'
						>
							Back
						</Button> : null
					}
					<div style={{flexGrow: 1}}></div>

					{currentStep !== 2 ? 
						<Button 
							variant="contained" 
							onClick={setStepForward} 
							id="proposal_next" 
							color="primary" 
							className='button'>
							Next
						</Button> :

						<React.Fragment>
							<Button
								variant="contained"
								id="proposal_approve_btn"
								color="primary"
								className='button'
								onClick={approveProposal}
							>
								Approve
							</Button>
							<Button
								variant="contained"
								disabled
								id="proposal_approve_btn"
								color="primary"
								className={"button withcomment"}
								onClick={approveProposalWithConditions}
							>
								Approve with conditions
							</Button>
							<Button
								variant="contained"
								disabled
								id="proposal_approve_btn"
								color="primary"
								className={"button disapprove"}
								onClick={disapproveProposal}
							>
								Disapprove
							</Button>
						</React.Fragment>

					}
				</FloatingFooter>
				<ProposalApprovalModal
					id="proposal_approval_modal"
					isModalOpen={isApprovalModalOpen}
					title={approvalModalTitle}
					message={approvalModalMessage}
					onClose={showHideApprovalModal}
					buttons={approvalModalButtons}
					allowRevision={allowRevision}
					setAllowedRevise={updateAllowedRevise}
					allowedRevise={allowedRevise}
				/>
			</div>
		);
	}
};

const mapStateToProps = (state: Store) => {
	return {
		benefitNodes: getBenefitNodes(state),
		clientData: getClientData(state),
		contractNode: selectContractNode(state),
		categoryNode: selectCategoryNode(state),
		summaryPackages: selectSummaryPackages(state),
		summaryBenefits: selectSummaryBenefits(state),
		selectedSummaryPackage: getSelectedSummaryPackage(state),
		selectedSummaryCategory: getSelectedSummaryCategory(state),
		planTypeList: selectPlanTypeList(state),
		roomAndBoardList: selectRoomAndBoardList(state),
		coverageList: selectCoverageList(state),
		riderList: selectRiderList(state),
		rows: selectRows(state),
		selectedPlanType: getSelectedPlanType(state),
		selectedPlanPackage: getSelectedPlanPackage(state),
		selectedBenefitCategory: getSelectedBenefitCategory(state),
		businessData: selectBusinessData (state),
		natureData: selectNatureData (state),
		contactDetailsData: selectContactDetailsData (state),
		HMOData: selectHMOData (state),
		principalData: selectPrincipalData(state),
		newNodeCounter: getNewNodeCounter(state),
		exclusionList: getExclusionList(state),
	}
};


const mapDispatchToProps = (dispatch: Dispatch) => ({
	SetBenefitNodes: bindActionCreators(ApproveProposalActions.SetBenefitNodes, dispatch),
	SetNewNodeCounter: bindActionCreators(ApproveProposalActions.SetNewNodeCounter, dispatch),
	SetClientData: bindActionCreators(ApproveProposalActions.SetClientData, dispatch),
	SetSelectedSummaryPackage: bindActionCreators(ApproveProposalActions.SetSelectedSummaryPackage, dispatch),
	SetSelectedSummaryCategory: bindActionCreators(ApproveProposalActions.SetSelectedSummaryCategory, dispatch),
	SetSelectedPlanType: bindActionCreators(ApproveProposalActions.SetSelectedPlanType, dispatch),
	SetSelectedPlanPackage: bindActionCreators(ApproveProposalActions.SetSelectedPlanPackage, dispatch),
	SetSelectedBenefitCategory: bindActionCreators(ApproveProposalActions.SetSelectedBenefitCategory, dispatch),
	SetExclusionList: bindActionCreators(ApproveProposalActions.SetExclusionList, dispatch),
});

export { mapStateToProps, mapDispatchToProps };