import { defaultProps } from './Constants';
import { ICategorySubForm } from './ICategorySubForm';

export const SpecialDiagnosticForm: React.FC<ICategorySubForm.IProps> = (
	props: ICategorySubForm.IProps,
): JSX.Element | null => {
	const { formHeader, rows, viewOnly, subFormNode, setSubForm } = props;

	console.log(rows);

	let metadata:any = {};
	if (subFormNode) {
		if (subFormNode['custom_metadata']) {
			metadata = subFormNode['custom_metadata'];
		}
	}

	// const fakeRows: any = [
	// 	{
	// 		procedure: 'Chest X-Ray for chest pains',
	// 		limit_type: ['mbl_limit'],
	// 		limit: [null],
	// 	},
	// 	{
	// 		procedure: 'Abdominal X-Ray for gastrointestinal diseases',
	// 		limit_type: ['abl_limit'],
	// 		limit: [null],
	// 	},
	// 	{
	// 		procedure: 'Electrocardingram for heart disease',
	// 		limit_type: ['pec_limit'],
	// 		limit: [1000],
	// 	},
	// 	{
	// 		procedure: 'Ultrasound',
	// 		limit_type: ['disease_limit'],
	// 		limit: [1000],
	// 	},
	// ];

	const columns: any = [
		{ 
			name: 'name', 
			title: 'Procedure' 
		},
		{ 
			name: 'limit_type', 
			title: 'Limit Type',
			getCellValue: row => (row.limits
				? row.limits : [])  
		},
		{ 
			name: 'limit', 
			title: 'Inner Limit',
			getCellValue: row => (row.limits
				? row.limits : [])  
		},
	];

	const formattedColumns = {
		limit: ['limit'],
		limit_type: ['limit_type'],
	};

	const columnExtensions = [
		{ columnName: 'limit_type', width: 150, wordWrapEnabled: true },
		{ columnName: 'limit', width: 150, wordWrapEnabled: true },
	];

	return (
		<React.Fragment>
			<Grid item sm={12}>
				<Grid container spacing={2} style={{ marginTop: '27.5px' }}>
					{!viewOnly ? formHeader : null}
				</Grid>
			</Grid>
			<Grid item sm={12}>
				<BenefitsTable
					viewOnly={viewOnly}
					columns={columns}
					formattedColumns={formattedColumns}
					columnExtensions={columnExtensions}
					{...props}
				/>
			</Grid>
			<Grid item sm={12}>
				<Typography variant="overline" display="block" gutterBottom>
					Limit
				</Typography>
				{
					!viewOnly ? 
						<React.Fragment>
							<Typography variant="body1" gutterBottom>
								Peso Limit for all procedures
							</Typography>
							<Typography variant="caption" gutterBottom>
								The amount specified for the particular procedure will override this amount
							</Typography>
							<PesoField 
								id="amt_limit"
								fieldName="amt_limit"
								selectName="amt_limit_unit"
								fieldValue={
									metadata['amt_limit'] !== undefined ? 
									metadata['amt_limit'] : 0
								}
								selectValue={
									metadata['amt_limit_unit'] !== undefined ?
									metadata['amt_limit_unit'] : 'php_mbl'
								}
								fieldOnChange={setSubForm}
								selectOnChange={setSubForm}
							/>
						</React.Fragment> :
						<ViewField 
							label="Peso Limit for all procedures"
							value={'PHP 2,000'}
						/>
				}
			</Grid>
		</React.Fragment>
	);
};

SpecialDiagnosticForm.defaultProps = defaultProps;